*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

.content{
  height: 100vh;
}
.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.filters{
  display: flex;
  /* align-items: center; */
  justify-content: space-between; 
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

.anticon svg{
  font-size:20px;
  cursor: pointer;
}

.active-icon{
  color:black;
}
.inactive-icon{
  color: gray;
}
.switch-icons{
  box-shadow: 1px solid rgba(0, 0, 0, 0.677);
  border: 5px;
  padding: 10px 15px;

}